nav {
  width: 100%;
  min-height: 56px;
  padding: 0 5%;
  background-color: aquamarine;
}

nav ul {
  display: flex;
  flex-direction: column;
  float: none;
}

nav ul li {
  list-style: none;
  width: 100%;
}

nav ul li a {
  display: block;
  text-decoration: none;
  text-align: center;
  color: #0D1E4C;
  padding: 1em;
}

nav ul li a:hover {
  color: #383333;
  border-bottom: 3px solid #383333;
  /* border-top: 3px solid #383333;*/
  text-decoration: none;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 68px;
  height: auto;
  font-size: 3vh;
  border: none;
  outline: none;
  cursor: pointer;
  align-self: flex-end;
}

.button img {
  width: 68px;
  height: auto;
}

#myLinks {
  display: none;
}

@media screen and (min-width: 1020px) {
  #myLinks {
    display: flex;
  }
  nav ul {
    display: flex;
    flex-direction: row;
  }
  .button {
    display: none;
  }
}

html, html {
  font-family: 'Bernina Sans', 'RobotoDraft', 'Helvetica Neue', Helvetica, Arial;
}

@font-face {
  html, html {
    font-family: "Bernina Sans";
    src: url("../fonts/BerninaSans-Web-CondensedBold.woff2") format("woff2");
  }
}

header {
  width: 100%;
  min-height: 500px;
  background-image: url("../img/background.jpg");
  background-position: center;
  background-size: cover;
  position: relative;
}

p {
  margin: 0;
}

header img {
  width: 100%;
  height: auto;
}

.reduction {
  position: absolute;
  width: 230px;
  background-color: darkred;
  color: white;
  transform: rotate(40deg);
  top: 70px;
  right: 30px;
  text-align: center;
  border-radius: 5px;
  display: inline;
}

.reduction > p, .reduction h3 {
  display: inline;
}

.header_reduction-inline {
  font-size: 15px;
}

.bandeau {
  position: absolute;
  bottom: 30px;
  background-color: gray;
  color: white;
  opacity: 0.5;
  width: 100%;
  padding: 10px;
}

.header_bandeau-inline {
  font-size: 10px;
}

.inner {
  padding: 0 2%;
  color: #00465b;
  background-color: #dde5eb;
}

.rendez-vous {
  margin: 5px 5px 0 auto;
  background-color: white;
  border-radius: 5px;
  display: flex;
  float: right;
  border: 1px solid grey;
}

.rendez-vous a {
  color: #00465b;
}

.main_content {
  text-align: center;
}

.main_title {
  text-align: center;
}

.ice-experience {
  width: 100%;
  letter-spacing: 5px;
  margin: 20px 0;
  text-align: right;
}

.container_flex-cat {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  padding: 5%;
}

.container_flex-cat > div {
  padding: 10px;
  text-align: center;
  font-size: 20px;
  width: 100%;
}

.container_flex-cat > div img {
  width: 100%;
  height: auto;
  border-radius: 50%;
  margin-bottom: 20px;
}

.container_flex-cat_ace {
  display: none;
}

@media screen and (min-width: 669px) {
  header {
    min-height: 800px;
  }
  .container_flex-cat_gym {
    order: -2;
  }
  .container_flex-cat > div {
    width: 50%;
  }
  .container_flex-cat_ace {
    display: block;
    width: 100%;
    height: 40px;
    border-radius: 55%;
    background: radial-gradient(#9db4c5, #dde5eb);
  }
}

@media screen and (min-width: 1020px) {
  header {
    background-position: initial;
  }
  .inner {
    padding: 0 20%;
  }
  .bandeau p {
    display: inline;
  }
  .container_flex-cat > div {
    width: 33%;
    margin: auto;
  }
  .container_flex-cat_ace {
    display: none;
  }
}
